
@font-face {
    font-family: 'Helvetica Neue Condensed Bold';
    src: url(/static/media/HelveticaNeue-CondensedBold.a6f72b5b.ttf)
         format('truetype'),
         url(/static/media/HelveticaNeue-CondensedBold.168da9fd.woff2)
         format('woff2');
 }

 @font-face {
     font-family: 'Helvetica Neue Regular';
     src: url(/static/media/HelveticaNeue-Regular.c8c88e6e.ttf)
          format('truetype'),
          url(/static/media/HelveticaNeue-Regular.b9b19cf4.woff2)
          format('woff2');
 }

 @font-face {
     font-family: 'Helvetica Neue Light';
     src: url(/static/media/HelveticaNeue-Light.dcf2fb06.ttf)
          format('truetype'),
          url(/static/media/HelveticaNeue-Light.4ce9031e.woff2)
          format('woff2');
 }

 @font-face {
     font-family: 'Helvetica Neue Medium';
     src: url(/static/media/HelveticaNeue-Medium.f41a121f.ttf)
          format('truetype'),
          url(/static/media/HelveticaNeue-Medium.f1ff47ea.woff2)
          format('woff2');
 }

 @font-face {
     font-family: 'Helvetica Neue Bold';
     src: url(/static/media/HelveticaNeue-Bold.35759a36.ttf)
          format('truetype'),
          url(/static/media/HelveticaNeue-Bold.4bdfe49c.woff2)
          format('woff2');
 }

* {
     transition: 0.32s;
 }

body {
  background: #DB9E24;
  margin: 0;
  padding: 0;

  -webkit-animation: 2s linear 0s 1 changeBackgroundColor;
          animation: 2s linear 0s 1 changeBackgroundColor;
}

@-webkit-keyframes changeBackgroundColor {
    0% {
        background: #B42222;
    }
    50% {
        background: #DB9E24;
    }
}

@keyframes changeBackgroundColor {
    0% {
        background: #B42222;
    }
    50% {
        background: #DB9E24;
    }
}

#root {
    display: flex;
}


.Div-SocialMedia-Links {
    display: flex;
    flex-wrap: wrap;
    grid-row-gap: 0.75em;
    row-gap: 0.75em;
    -webkit-column-gap: 0.75em;
       -moz-column-gap: 0.75em;
            grid-column-gap: 0.75em;
            column-gap: 0.75em;
}

.Div-SocialMedia-LinkItem {
    display: inline;
    line-height: 0;
    transition: 0.64s;
}

.Div-SocialMedia-LinkItem:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.32s;
}

.Image-SocialMedia-LinkItem {

}


.Div-Other-Links {
    display: flex;
    flex-direction: column;
     grid-row-gap: 0.0625em;
     row-gap: 0.0625em;
}

.A-Other-LinkItem {
    font-family: 'Helvetica Neue Medium', Helvetica;
    font-size: 0.875em;
    line-height: 1.3125em;
    letter-spacing: 0.01em;
    text-decoration: none;
    text-transform: uppercase;
    max-width: 36em;
    margin: 0;
    padding-top: 0.25em;
    padding-bottom: 0.25em;
    color: #A1A2AF;
}

.A-Other-LinkItem:hover {
    opacity: 0.64;
    transition: 0.32s;
}

.A-Other-LinkItem:after {
    content: "  \2794";
    margin-left: 3px;
}

@media only screen and (max-width: 760px) {

    .A-Other-LinkItem {
        font-family: 'Helvetica Neue Bold', Helvetica;
    }
}


.Main {
    display: flex;
    flex-direction: column;
    grid-row-gap: 2em;
    row-gap: 2em;
    width: 100%;
    margin: 0;
    margin-bottom: 6em;
    padding: 6em;
    padding-bottom: 4em;
    background: #E1E3E9;
    color: #20232D;
    -webkit-animation: 1s linear 0s 1 slideMain;
            animation: 1s linear 0s 1 slideMain;
}

@-webkit-keyframes slideMain {
    0% {
        -webkit-transform: translateY(-150%);
                transform: translateY(-150%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideMain {
    0% {
        -webkit-transform: translateY(-150%);
                transform: translateY(-150%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.Paragraph-Name {
    font-family: 'Helvetica Neue Condensed Bold', Helvetica;
    font-size: 6em;
    line-height: 0.75em;
    letter-spacing: -0.05em;
    margin: 0;
    margin-left: -0.05em;
    color: #20232D;
}

.Paragraph-Description {
    font-family: 'Helvetica Neue Light', Helvetica;
    font-size: 1em;
    line-height: 1em;
    letter-spacing: 0.01em;
    margin: 0;
    margin-right: auto;
    margin-bottom: -3px;
    padding: 0.625em;
    padding-left: 0.875em;
    padding-right: 0.875em;
    background: #20232D;
    color: #E1E3E9;
}

.Paragraph {
    font-family: 'Helvetica Neue Regular', Helvetica;
    font-size: 1em;
    line-height: 1.5em;
    letter-spacing: 0.01em;
    max-width: 36em;
    margin: 0;
    margin-top: -5px;
    margin-bottom: -5px;
    color: #20232D;
}

@media only screen and (max-width: 760px) {
    .Main {
        padding: 2em;
    }

    .Paragraph-Description {
        font-size: 0.875em;
        line-height: 1.3125em;
        letter-spacing: 0.01em;
    }

    .Paragraph {
        font-size: 0.875em;
        line-height: 1.3125em;
        letter-spacing: 0.01em;
    }
}

